.contact {
    font-family: 'Architects Daughter', cursive;
    background: url("https://wallpaperaccess.com/full/983279.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

.contact-image {
    background: url("https://img6.gram.pl/jnwy20200415113437188kfnx.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    height: 50vh;
    text-align: start;
    position: relative;
    overflow: hidden;
    padding: 1em;
}

.contact h1 {
    padding-top: 2em;
    margin: 0;
    padding-left: 5%;
    text-shadow: 0 0.1em 0.1em black;
}

.contact-container {
    display: flex;
    padding: 1em;
    justify-content: space-around;
}

.contact-container .contact-container__form {
    width: 50%;
}

.contact-container .contact-container__description {
    text-align: left;
    margin-left: 5em;
}

.contact-container .contact-container__description span {
    color: #b39809;
}

.contact-container .contact-container__description p {
    padding: 0.5em;
    color: white;
}

.contact-container .contact-container__description ul {
    list-style: circle;
    color: white;
    margin: auto;
}

.contact .contact-map {
    padding: 1em 3em;
}